const OptionColor = ["primary", "blue", "red", "yellow", "black", "white", "pink"];
const OptionSize = ["huge", "large", "medium", "small", "mini", "tiny"];
const optionProps = {
  color: {
    type: String,
    validator(value) {
      return OptionColor.includes(value);
    },
    default: "primary"
  },
  size: {
    type: String,
    validator(value) {
      return OptionSize.includes(value);
    },
    default: "medium"
  },
  disabled: {
    type: Boolean,
    default: false
  }
};
export {
  OptionColor,
  OptionSize,
  optionProps
};
